import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: () => import('@/views/personCenter/index')
    },{
			path: '/404',
			component: () => import('@/views/404')
		},{
			path: '*',
			redirect: '/404'
		}]
})



